
import { defineComponent } from "vue"

export default defineComponent({
  name: `Header`,
  components: {},
  data: () => ({
    activity: ``,
  }),
  props: [`start`, `submitActivity`, `startDay`],
  methods: {
    onSubmit() {
      this.$emit(`submitActivity`, this.activity)
      this.activity = ``
    },
    startTimer() {
      this.startDay()
    },
  },
  mounted() {
    const submitButton = document.querySelector(`#submit-button`)

    let touchTimer: any

    // submitButton?.addEventListener(`touchstart`, function (event) {
    submitButton?.addEventListener(`mousedown`, function (event) {
      touchTimer = setTimeout(function () {
        console.log(`Long touch!`)
      }, 1000)
    })

    // submitButton?.addEventListener(`touchend`, function (event) {
    submitButton?.addEventListener(`mouseup`, function (event) {
      clearTimeout(touchTimer) // Clear the timer if touch is released before the specified duration
    })
  },
})
